.location-popup {
  @include flex-center-column;
  align-items: flex-start;
  gap: 32px;

  &__input {
    width: 100%;

    &-wrapper {
      position: relative;
      @include flex-center-column;
      gap: 0;

      width: 472px;
    }

    &-area {
      width: 100%;
      @include flex-center-column;
      align-items: flex-start;
    }
  }

  &__buttons {
    @include flex-center;
    justify-content: flex-end;
    width: 100%;
  }

  &__clear-icon {
    position: absolute;
    top: 12px;
    right: 14px;

    &:hover {
      background-image: url('./../../../public/clear-hover.svg');
      transition-duration: $effect-duration;
      transition-property: background-image;
    }
  }
}
